body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 0;
  color: #294677;
  font-weight: bold;
  background-color: #172b4d;
}

ol,
ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #294677;
  border: 1px solid #172b4d;
  float: left;
  font-size: 3rem;
  font-weight: bold;
  /* line-height: 34px; */
  min-height: 4rem;
  min-width: 4rem;
  /* margin-right: -1px;
  margin-top: -1px; */
  padding: 0;
  text-align: center;

  color: #172b4d;
  align-items: unset;
}

.game {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 15px;
}

.game-info {
}

.game .game-board {
  text-align: center;
}

button {
  background-color: #f0f8ff00;
  border: 1px solid #294677;
  color: #294677;
}

button:focus {
  outline: none;
}
